import { Modal } from "@chac/common/components/Modal";
import styled from "styled-components";

import React from "react";
import { useInput } from "../hooks/useInput";
/**
 * Dimmed 등 기본적인 modal css를 위해 <Modal type="custom" > 로 감싸준다
 */

const StyledContainer = styled.div`
	input {
		color: blue;
		background: white;
		padding: 5px 10px;
		border-bottom: solid 1px;
		border-radius: 15px;
	}
	input:read-only{
		color: gray;
		background: #d2d2d2;
	}
	button {
		background: blue;
		color: white;
		padding: 10px;
		border-radius: 20px;
	}
`;

export default function CustomSampleModal({data, close}) {
	const [{ number }, onChange, reset] = useInput({
		number: data.props.number,
	  });
	console.log(data)
	// 종료
	const btnCloseClick = () => {
		const result = {
			number
		}
		console.log('이 팝업에서 받은 데이터>>>>>', data);
		console.log('이 팝업에서 보내는 데이터>>>>>', result);
		// close 함수를 호출함으로서 closeHandler에 데이터 전달 및 팝업 종료
		close(result);
	}
	return (
		<Modal type="bottomSheet" close={close}>
			<StyledContainer>
				<h2>테스트용 CustomMoal</h2>
				<div>
					<h4>이름</h4>
					<input type="text" name="name" value={data.props.name} readOnly/>
				</div>
				<div>
					<h4>나이</h4>
					<input type="text" name="age" value={data.props.age} readOnly/>
				</div>
				<hr />
				<div>보낼 데이터</div>
				<div>
					<input type="text" name="number" value={number} onChange={onChange}/>
				</div>

				<button onClick={btnCloseClick}>종료버튼</button>

			</StyledContainer>
		</Modal>
	);
}
